$orange: #e8663c;
$container-main: 675;

.to-top {
  background-color: darken($orange, 10%); }

img {
  max-width: 100%; }

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1.5; }

.container {
  @include container;

  &__wrap {
    @include df;
    justify-content: space-between; }

  &__main {
    @include w(675, 940);

    @include to(700) {
      width: 60%; }

    @include to(550) {
      width: 100%;
      margin-bottom: 40px; } }

  &__sidebar {
    @include w(220, 940);

    @include to(700) {
      width: 35%; }

    @include to(550) {
      width: 100%; } } }

.content,
.article {
  margin-bottom: 47px; }

.b-text {

  img {
    float: left;
    padding: 0 20px 20px 0; }

  p {
    padding-bottom: 15px; }

  blockquote {
    display: flex;
    margin-bottom: 25px;

    font-style: italic;

    color: lighten(#333, 20%);

    &:before {
      content: '';
      width: 30px;
      margin-right: 15px;

      background: $orange; } } }


.two-col {
  column-count: 2;
  column-gap: 20px;

  @include to(700) {
    column-count: 1; } }

.three-col {
  column-count: 3;
  column-gap: 20px;

  @include to(700) {
    column-count: 1; } }

.info {
  @include df;
  justify-content: center;
  align-items: center;

  border: 1px solid #4d8fcb;
  padding: 20px;
  margin-bottom: 15px;

  background-color: #d1e4f3;
  color: #4d8fcb;

  &-success {
    background-color: #effeb9;
    border: 1px solid #98c601;
    color: #98c601; }

  &-error {
    background-color: #fbcbc1;
    border: 1px solid #eb5439;
    color: #eb5439; }

  &-attention {
    background-color: #ffe9ad;
    border: 1px solid #fab418;
    color: #fab418; } }


.drop-cap::first-letter {
  float: left;

  padding-right: 5px;

  font-size: 36px;
  line-height: 1;
  font-weight: bold;

  color: $orange; }






.h3, .h4 {
  margin-top: 0;
  margin-bottom: 22px;

  @include bebas;
  font-size: 18px;

  color: #DF5E42;

  &.stroke:after {
    margin-left: 20px; } }

.h4 {
  margin-bottom: 15px;
  font-size: 14px; }


.stroke {
  display: flex;

  &:after {
    content: '';
    flex-grow: 1;
    padding: 5px 0;

    background-image: linear-gradient(-45deg, #C2C2C2 25%, rgba(#fff, 0) 0, rgba(#fff, 0) 50%, #C2C2C2 0, #C2C2C2 75%, rgba(#fff, 0) 0);
    background-size: 3px 3px;
    background-clip: content-box; } }


.button {
  display: inline-block;
  height: 31px;
  padding: 0 13px;

  @include bebas;
  font-size: 12px;
  line-height: 31px;

  text-transform: uppercase;
  text-decoration: none;
  background: $orange;
  color: #fff;

  transition: background, .3s;

  &:hover {
    background: rgba($orange, .8); } }



