.footer {
  overflow: hidden;

  &__wrap {
    @include df;
    padding: 32px 25px 20px;

    background: $orange;
    color: #fff;

    position: relative;

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;

      margin-left: 10px;
      width: 1000px;
      height: 100%;

      background-color: rgba(232, 96, 60, 0.5); }

    &:after {
      left: auto;
      right: 100%;
      margin-right: 10px; } }

  &__col {
    @include col;
    @include sz(4);
    padding-right: 20px;

    @include to(800) {
      @include sz(6);
      margin-bottom: 30px; }

    @include to(550) {
      @include sz(12); }

    &:last-child {
      @include to(800) {
        margin-bottom: 0; } } } }


.footer__title {
  margin-top: 0;
  margin-bottom: 20px;

  @include bebas;
  font-size: 20px;
  text-transform: uppercase; }

.footer__text {
  font-family: Arial, Helvetica, sans-serif; }

.footer__contact-links {
  margin-bottom: 20px;

  a,
  a:hover {
    display: block;
    text-decoration: none;
    color: #fff; } }


.tweet {
  margin-bottom: 2em;

  &:last-child {
    margin-bottom: 0; }

  &__content {
    margin-bottom: 5px; }

  a,
  a:hover {
    text-decoration: none;
    color: #f3b29d; } }


.soc-icon {
  width: 30px;
  height: 31px;
  margin-right: 3px;

  text-decoration: none;

  &__hover {
    text-decoration: none; } }
