.sidebar__widget {
  margin-bottom: 30px;

  font-family: Arial, Helvetica, sans-serif; }

.nav-category {
  font-family: Arial, Helvetica, sans-serif;

  &__list {
    padding: 0;
    list-style: none; }

  &__item a {
    font-weight: bold;

    text-decoration: none;
    color: #464646;

    &:hover {
      text-decoration: underline; } } }


.small-gallery {
  $fill: 3px;
  @include df;
  margin-right: -1*$fill;
  margin-left: -1*$fill;

  &__item {
    width: calc(25% - 2 * #{$fill});
    margin-right: $fill;
    margin-left: $fill;
    margin-bottom: $fill - 2;

    cursor: pointer;

    img {
      max-width: 100%;
      width: 100%; } } }
