.nav {
  @include df;
  @include row-flex;
  justify-content: center; }

.nav__item {
  margin: 0 13px;
  padding-top: 10px;
  padding-bottom: 10px;

  font-size: 16px;

  color: #b9b9b9;
  text-transform: uppercase;
  text-decoration: none;

  transition: color, .3s;

  &:hover,
  &-is-active {
    color: $orange;
    text-decoration: none; } }
