.works {
  @include df;
  @include row-flex;

  &__item {
    @include col;
    @include sz(3);

    @include to(800) {
      @include sz(6);
      margin-bottom: 20px; }

    @include to(400) {
      @include sz(12); } }

  &__item-grid {
    margin-bottom: 40px; } }

.work {
  text-decoration: none;

  &:hover {
    text-decoration: none; }

  &__img {
    position: relative;

    padding-bottom: 83%;

    background-size: cover; }

  &__content {
    padding: 12px 15px 11px;
    border: 1px solid rgba(#B8B8B8, .5);
    border-top: 0;

    @include bebas; }

  &__title {
    margin-bottom: 1px;
    font-size: 16px;
    line-height: 18px;

    color: #B8B8B8; }

  &__date {
    font-size: 11px;
    line-height: 18px;
    color: $orange; } }


.work__overlay {
  position: absolute;

  width: 100%;
  height: 100%;

  background: rgba(#000, .6);
  opacity: 0;

  transition: opacity, .3s;

  & span {
    @include vcenter;

    width: 40px;
    height: 40px;
    border-radius: 1px;

    background: rgba(#fff, .7); }

  $plus-line-w: 20px;
  $plus-line-h: 5px;

  & span:before,
  & span:after {
    content: '';
    @include vcenter;

    display: block;
    background: #3C3023; }

  & span:before {
    width: $plus-line-w;
    height: $plus-line-h; }

  & span:after {
    width: $plus-line-h;
    height: $plus-line-w; }

  &:hover {
    opacity: 1; } }
