.slider {
  position: relative;

  &__prev,
  &__next {
    position: absolute;
    top: 45%;
    left: 20px;
    transform: translateY(-50%);
    z-index: 100;

    @include to(800) {
      @include vh; } }

  &__next {
    left: auto;
    right: 20px;
    transform: translateY(-50%) scale(-1, 1); }

  .icon {
    width: 36px;
    height: 52px; } }

.slide {
  position: relative;
  width: 100%;
  padding-bottom: 42.5%;

  background-size: cover;

  &__content {
    position: absolute;
    left: 35px;
    bottom: 35px;

    max-width: 375px;
    padding: 20px 20px 10px;

    @include to(600) {
      left: 50%;
      bottom: 50%;
      transform: translate(-50%, 50%);
      width: 80%;
      padding: 10px; }

    background-color: rgba($orange, .8);
    color: #ffffff; }

  &__title {
    margin-bottom: 12px;

    @include bebas;
    font-size: 18px;

    text-transform: uppercase; }

  &__text {
    font-family: Arial, Helvetica, sans-serif; } }


.thumbs {
  @include df;
  @include row-flex; }

.thumb {
  @include col;
  @include sz(2);
  padding-bottom: 1px;

  cursor: pointer;

  @include to(750) {
    @include sz(4);
    margin-bottom: 20px; }

  @include to(420) {
    @include sz(6); }

  &__wrapper {
    padding-bottom: 87%;
    outline: 1px solid rgba(#333, .8);

    background-size: 100%;
    background-position: center;

    transition: outline, .3s;

    &:hover {
      outline: 1px solid rgba(#333, .8); } } }
