.map {
  position: relative;

  padding-bottom: 59.5%;
  margin-bottom: 20px;
  overflow: hidden;

  img {
    @include vcenter; } }

.contacts {
  $text-color: #414141;

  font-family: Arial, Helvetica, sans-serif;

  color: $text-color;

  &__wrap {
    @include df;
    justify-content: space-between; }

  &__text {
    @include w(185, $container-main);

    @include to(700) {
      width: 100%; } }

  .p-head {
    font-weight: bold;
    color: $orange; }

  a {
    text-decoration: none;
    color: $text-color;

    &:hover {
      text-decoration: none;
      color: $text-color; } }


  &__form {
    @include w(458, $container-main);

    @include to(700) {
      width: 100%; } } }
