.features {
  @include df;
  @include row-flex;
  justify-content: center;
  margin-bottom: 42px; }

.features__item {
  @include col;
  @include sz(4);

  @include to(800) {
    @include sz(6);
    margin-bottom: 30px; }

  @include to(550) {
    @include sz(12); }

  &:last-child {
    @include to(800) {
      margin-bottom: 0; } } }
