// font-family
@mixin font($alias, $name, $weigth: normal) {
  @font-face {
    font-family: $alias;
    src: url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".woff") format("woff");
    font-weight: $weigth;
    font-style: normal; } }
// to use:
//+font(black, cytiapro-black-webfont)

@mixin w($col, $container) {
  width: round($col / $container * 100%); }

@mixin ie {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content; } }


// Display flex wrap
@mixin df {
  display: flex;
  flex-wrap: wrap; }

// .visually-hidden
@mixin vh() {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

// Center container
@mixin vcenter() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@mixin bg {
  background: rgba(pink, .4); }

//flip-horizontal
@mixin fg {
  transform: scale(-1, 1); }

//flip-vertical
@mixin fg {
  transform: scale(1, -1); }

//placeholder
@mixin placeholder {
  &::placeholder {
    @content; } }

@mixin placeholder-color($color) {
  &::placeholder {
    color: $color; } }


