.nav-cards {
  margin-bottom: 33px;

  &__list {
    margin: 0;
    padding: 0;
    @include df;

    font-size: 14px;


    @include to(550) {
      justify-content: center; }

    @include bebas;

    color: #b9b9b9;
    list-style: none; }

  &__item {
    padding: 7px 13px;

    cursor: pointer;
    transition: background, color, .3s;

    &:hover,
    &-is-active {
      background: $orange;
      color: #fff; } } }
