.hero {
  overflow: hidden;
  margin-bottom: 25px; }

.hero-short {
  margin-bottom: 25px;

  .hero__wrap {
    margin-bottom: 0; } }

.hero__wrap {
  position: relative;
  margin-bottom: 12px;

  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 100%;

    margin-left: 10px;
    width: 1000px;
    height: 60px;

    background-color: rgba(232, 96, 60, 0.5); }

  &:after {
    left: auto;
    right: 100%;
    margin-right: 10px; } }

.hero__title {
  height: 60px;
  margin: 0;
  padding-left: 20px;

  @include bebas;
  font-size: 24px;
  line-height: 60px;
  text-transform: uppercase;
  white-space: nowrap;

  background: $orange;
  color: #fff; }
