.anonce {
  position: relative;
  margin-bottom: 30px;

  &__icon {
    position: absolute;
    top: 0;
    right: 100%;

    width: 80px;
    height: 80px;
    margin-right: 10px;

    background: $orange; }

  &__img {
    position: relative;

    display: block;
    padding-bottom: 44%;
    margin-bottom: 20px; }

  &__imgwrap {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    width: 100%;

    img {
      width: 100%; } }

  &__content {
    @include df; }

  &__meta {
    @include w(140, $container-main);

    @include to(700) {
      width: 100%;
      margin-bottom: 20px; } }

  &__text {
    @include w(525, $container-main);

    @include to(700) {
      width: 100%; } }

  &__title {
    width: 100%;
    margin-top: 0;

    @include bebas;
    font-size: 18px;
    line-height: 1.3;

    color: $orange; }

  &__title > a {
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__subtitle {
    margin-top: -15px;

    color: #a0a0a0; }

  &__stroke {
    height: 20px;
    padding-top: 20px; }

  &__blockquote {
    box-sizing: border-box;
    display: flex;
    min-height: 80px;
    padding: 20px;
    margin: 0;

    &:before {
      content: '“';
      position: relative;
      top: 30px;

      margin-right: 15px;
      padding-left: 20px;

      font-family: Georgia, 'Times New Roman', Times, serif;
      font-size: 72px;
      line-height: 0;

      color: $orange; }



    margin-top: 0;
    background-color: whitesmoke; } }


.meta__item {
  margin-bottom: 10px;

  @include to(700) {
    display: flex; } }

.meta__title {
  margin: 0;
  margin-bottom: 5px;

  @include to(700) {
    margin-right: 10px; }

  @include bebas;

  color: $orange; }

.meta__descr {
  margin: 0;
  color: #bababa; }


.icon-anonce__wrap {
  @include vcenter;

  width: 28px;
  height: 33px; }

.icon-anonce__text {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 72px;
  line-height: 100%;

  color: #fff; }


