.form {

  label {
    @include vh; }

  &__item {
    margin-bottom: 10px; }

  input[type='text'],
  textarea {
    width: 100%;
    padding: 0 10px;
    outline: 1px solid $orange;
    outline-color: $orange;
    border: 0; }

  input[type='text'] {
    min-width: 200px;
    max-width: 200px;
    height: 33px;
    line-height: 33px; }

  textarea {
    box-sizing: border-box;
    max-width: 100%;
    min-width: 100%;
    padding-top: 10px; }

  input[type='submit'] {
    height: 30px;
    line-height: 30px;
    border: 1px solid darken($orange, 10%);
    padding: 0 15px;

    @include bebas;

    background: $orange;
    color: #fff;
    cursor: pointer; } }



@include placeholder {
  @include bebas;
  color: $orange; }


