.pagination {
  margin-bottom: 0;

  &__list {
    @include df;
    list-style: none; }

  &__item {
    padding: 5px 8px;
    margin-right: 3px;

    @include bebas;
    font-size: 14px;
    line-height: 1;

    background: lighten($orange, 20%);
    color: #fff; }

  &__item-is-active {
    background: $orange; }


  a {
    text-decoration: none;
    color: inherit; } }
