.feature {
  &__header {
    display: flex;
    align-items: center; }

  &__icon {
    width: 70px;
    flex-shrink: 0; }

  .icon {
    width: 46px;
    height: 46px; }

  &__title {
    @include bebas;
    font-size: 18px;
    line-height: 24px;

    text-transform: uppercase;
    text-decoration: none;
    color: $orange;

    &:hover {
      text-decoration: none; } }

  &__content {
    margin-bottom: 15px;
    font-family: Arial, Helvetica, sans-serif; } }
